import { addDoc, collection, deleteDoc, doc, getDocs, orderBy, query, updateDoc } from 'firebase/firestore';
import { firestoreDb } from './client';

export type BoardDoc = {
  id: string;
  name: string;
  description?: string;
  instanceId?: string;
  createdAt: number;
  updatedAt: number;
};

export function boardsRef(workspaceId: string) {
  const db = firestoreDb();
  return collection(db, 'workspaces', workspaceId, 'boards');
}

export async function listBoards(workspaceId: string): Promise<BoardDoc[]> {
  const q = query(boardsRef(workspaceId), orderBy('createdAt', 'desc'));
  const snap = await getDocs(q);
  return snap.docs.map((d) => ({ id: d.id, ...(d.data() as Omit<BoardDoc, 'id'>) }));
}

export async function createBoard(workspaceId: string, input: Omit<BoardDoc, 'id' | 'createdAt' | 'updatedAt'>) {
  const ref = await addDoc(boardsRef(workspaceId), {
    ...input,
    createdAt: Date.now(),
    updatedAt: Date.now(),
  });
  return ref.id;
}

export async function updateBoard(workspaceId: string, boardId: string, patch: Partial<BoardDoc>) {
  const db = firestoreDb();
  await updateDoc(doc(db, 'workspaces', workspaceId, 'boards', boardId), {
    ...patch,
    updatedAt: Date.now(),
  });
}

export async function deleteBoard(workspaceId: string, boardId: string) {
  const db = firestoreDb();
  await deleteDoc(doc(db, 'workspaces', workspaceId, 'boards', boardId));
  // Note: cards deletion should also be handled (server/admin). We'll add that shortly.
}
