'use client';

import { useEffect, useMemo, useState } from 'react';
import { NavShell } from '@/app/components/NavShell';
import { useWorkspace } from '@/lib/firebase/useWorkspace';
import { listBoards } from '@/lib/firebase/boards';
import { createCard } from '@/lib/firebase/cards';
import { firebaseAuth } from '@/lib/firebase/client';

type Suggestion = { text: string };

type UiBoard = {
  id: string;
  name: string;
};

function isE2EBypass() {
  // Allow E2E bypass even under next start (NODE_ENV=production) *only* when explicitly enabled.
  if (process.env.NODE_ENV === 'production' && process.env.NEXT_PUBLIC_E2E_BYPASS_AUTH !== '1') return false;
  if (process.env.NEXT_PUBLIC_E2E_BYPASS_AUTH === '1') return true;
  if (typeof window !== 'undefined') {
    try {
      return new URLSearchParams(window.location.search).get('e2e') === '1';
    } catch {
      return false;
    }
  }
  return false;
}

export default function IntakePage() {
  const { workspaceId, authReady } = useWorkspace();

  const [text, setText] = useState('');
  const [result, setResult] = useState<Suggestion[]>([]);
  const [boardId, setBoardId] = useState('');
  const [loading, setLoading] = useState(false);
  const [boards, setBoards] = useState<UiBoard[]>([]);
  const [adding, setAdding] = useState(false);

  useEffect(() => {
    if (!workspaceId) return;
    let cancelled = false;
    (async () => {
      const b = await listBoards(workspaceId);
      if (cancelled) return;
      setBoards(b.map((x) => ({ id: x.id, name: x.name })));
    })().catch(() => {
      // ignore
    });
    return () => {
      cancelled = true;
    };
  }, [workspaceId]);

  const canExtract = useMemo(() => !!text.trim() && !!boardId && !!workspaceId, [text, boardId, workspaceId]);

  async function extractTasks() {
    const trimmed = text.trim();
    if (!trimmed) return;
    if (!boardId) {
      alert('Select a board first.');
      return;
    }
    if (!workspaceId) {
      alert('Missing workspace.');
      return;
    }

    setLoading(true);
    try {
      let headers: Record<string, string> = { 'content-type': 'application/json' };

      if (!isE2EBypass()) {
        const user = firebaseAuth().currentUser;
        if (!user) {
          alert('You must be signed in.');
          return;
        }
        const token = await user.getIdToken();
        headers = { ...headers, authorization: `Bearer ${token}` };
      }

      const endpoint = isE2EBypass() ? '/api/intake/extract?e2e=1' : '/api/intake/extract';
      const resp = await fetch(endpoint, {
        method: 'POST',
        headers,
        body: JSON.stringify({ text: trimmed, boardId }),
      });
      if (!resp.ok) {
        const err = await resp.json().catch(() => ({}));
        throw new Error(err?.error || `extract_failed_${resp.status}`);
      }
      const data = (await resp.json()) as { candidates: string[] };
      setResult((data.candidates || []).map((t) => ({ text: t })));
    } catch (e) {
      const msg = e instanceof Error ? e.message : String(e);
      alert(`Extract failed: ${msg}`);
    } finally {
      setLoading(false);
    }
  }

  async function addAsTasks() {
    if (!boardId) {
      alert('Select a board first.');
      return;
    }
    if (!workspaceId) {
      alert('Missing workspace.');
      return;
    }
    if (!result.length) return;

    setAdding(true);
    try {
      const trimmedDesc = text.slice(0, 2000);

      // Create cards in Firestore (proposed)
      await Promise.all(
        result.map((r) =>
          createCard(workspaceId, boardId, {
            title: r.text.length > 80 ? r.text.slice(0, 77) + '…' : r.text,
            description: trimmedDesc,
            priority: 'P2',
            source: 'Manual',
            columnId: 'proposed',
            order: Date.now(),
            artifacts: [],
            approval: { status: 'NOT_REQUESTED' },
          })
        )
      );

      setResult([]);
      setText('');
      alert(`Added tasks to Proposed.`);
    } finally {
      setAdding(false);
    }
  }

  if (!authReady) {
    return (
      <NavShell title="Intake">
        <div className="panel p-4 text-[color:var(--muted)]">Loading…</div>
      </NavShell>
    );
  }

  return (
    <NavShell title="Intake">
      <div className="panel p-5">
        <div className="text-sm text-[color:var(--muted)]">
          Option A security: Intake text is processed by the app backend only (not sent to the bot) unless you explicitly
          add an “Ask bot” step later.
        </div>

        <div className="mt-4">
          <div className="text-xs uppercase tracking-wide text-[color:var(--muted)] font-extrabold">Target board</div>
          <select
            value={boardId}
            onChange={(e) => setBoardId(e.target.value)}
            className="mt-1 w-full rounded-2xl bg-[rgba(11,12,15,.7)] border border-[color:var(--stroke)] p-3 outline-none"
          >
            <option value="">Select a board…</option>
            {boards.map((b) => (
              <option key={b.id} value={b.id}>
                {b.name}
              </option>
            ))}
          </select>
          {boards.length === 0 ? <div className="mt-2 text-xs text-[color:var(--muted)]">Create a board first.</div> : null}
        </div>

        <div className="mt-4">
          <div className="text-xs uppercase tracking-wide text-[color:var(--muted)] font-extrabold">Input</div>
          <textarea
            value={text}
            onChange={(e) => setText(e.target.value)}
            placeholder="Paste an email thread or meeting transcript here…"
            className="mt-1 w-full min-h-[220px] rounded-2xl bg-[rgba(11,12,15,.7)] border border-[color:var(--stroke)] p-4 outline-none"
          />
          <div className="mt-3 flex flex-wrap gap-2">
            <button
              onClick={() => void extractTasks()}
              disabled={loading || !canExtract}
              className="btn-primary px-4 py-2 rounded-xl font-extrabold disabled:opacity-50"
            >
              {loading ? 'Extracting…' : 'Extract tasks'}
            </button>
            <button
              onClick={() => {
                setText('');
                setResult([]);
              }}
              className="px-4 py-2 rounded-xl border border-[color:var(--stroke)] bg-[rgba(22,26,34,.55)] font-bold"
            >
              Clear
            </button>
          </div>
        </div>
      </div>

      <div className="mt-4 panel p-5">
        <div className="flex items-center justify-between">
          <div className="text-xs uppercase tracking-wide text-[color:var(--muted)] font-extrabold">Suggested tasks</div>
          <div className="chip">{result.length}</div>
        </div>

        <div className="mt-3 grid gap-2">
          {result.length ? (
            result.map((r, idx) => (
              <div key={idx} className="card p-3">
                <div className="font-extrabold">{r.text}</div>
                <div className="mt-2 text-xs text-[color:var(--muted)]">Priority: P2 • Source: Intake</div>
              </div>
            ))
          ) : (
            <div className="text-[color:var(--muted)]">Nothing yet. Paste text and click “Extract tasks”.</div>
          )}
        </div>

        <div className="mt-3">
          <button
            disabled={!result.length || adding}
            onClick={() => void addAsTasks()}
            className={`px-4 py-2 rounded-xl font-extrabold ${
              result.length && !adding
                ? 'btn-primary'
                : 'border border-[color:var(--stroke)] bg-[rgba(22,26,34,.35)] text-[color:var(--muted)]'
            }`}
          >
            {adding ? 'Adding…' : 'Add to Proposed'}
          </button>
        </div>
      </div>
    </NavShell>
  );
}
