'use client';

import { useEffect, useState } from 'react';
import { onAuthStateChanged } from 'firebase/auth';
import { firebaseAuth } from './client';
import { ensureUserAndWorkspace } from './onboarding';

function isE2EBypass() {
  // Allow E2E bypass even under next start (NODE_ENV=production) *only* when explicitly enabled.
  if (process.env.NODE_ENV === 'production' && process.env.NEXT_PUBLIC_E2E_BYPASS_AUTH !== '1') return false;
  if (process.env.NEXT_PUBLIC_E2E_BYPASS_AUTH === '1') return true;
  if (typeof window !== 'undefined') {
    try {
      return new URLSearchParams(window.location.search).get('e2e') === '1';
    } catch {
      return false;
    }
  }
  return false;
}

export function useWorkspace() {
  const [workspaceId, setWorkspaceId] = useState<string | null>(() => (isE2EBypass() ? 'ws_e2e_user' : null));
  const [authReady, setAuthReady] = useState<boolean>(() => (isE2EBypass() ? true : false));

  useEffect(() => {
    if (isE2EBypass()) return;

    const unsub = onAuthStateChanged(firebaseAuth(), async (user) => {
      if (!user) {
        setWorkspaceId(null);
        setAuthReady(true);
        return;
      }
      const { defaultWorkspaceId } = await ensureUserAndWorkspace(user);
      setWorkspaceId(defaultWorkspaceId);
      setAuthReady(true);
    });
    return () => unsub();
  }, []);

  return { workspaceId, authReady };
}
