import { addDoc, collection, deleteDoc, doc, getDocs, orderBy, query, updateDoc } from 'firebase/firestore';
import { firestoreDb } from './client';

export type BlockOption = { id: string; label: string };

export type BlockPacket = {
  status: 'blocked';
  prompt: string;
  options: BlockOption[];
  requestedAt: number;
  requestedByInstanceId?: string;
};

export type UnblockAnswer = {
  answeredAt: number;
  answeredByUid?: string;
  optionId?: string;
  freeText?: string;
  // Snapshot what the agent asked for (helps audit even if we clear `blocked`).
  blockedPrompt?: string;
  blockedOptions?: BlockOption[];
};

export type ApprovalStatus = 'NOT_REQUESTED' | 'PENDING' | 'APPROVED' | 'CHANGES_REQUESTED';

export type ApprovalPacket = {
  status: ApprovalStatus;
  requestedAt?: number;
  requestedByInstanceId?: string;
  reviewedAt?: number;
  reviewedByUid?: string;
  note?: string;
};

export type CardDoc = {
  id: string;
  boardId: string;
  title: string;
  description?: string;
  priority: 'P0' | 'P1' | 'P2' | 'P3';
  source: 'TARS' | 'Email' | 'Meeting' | 'Manual';
  columnId: 'proposed' | 'queued' | 'doing' | 'review' | 'blocked' | 'done';
  order: number;
  artifacts?: unknown[];
  approval?: ApprovalPacket;
  blocked?: BlockPacket;
  unblock?: UnblockAnswer;
  startedDoingAt?: number;
  movedToReviewAt?: number;
  timeToReviewMs?: number;
  reviewDecision?: {
    outcome: 'approved' | 'changes_requested';
    decidedAt: number;
    decidedByUid?: string;
    instructions?: string;
  };
  createdAt: number;
  updatedAt: number;
};

function cardsRef(workspaceId: string, boardId: string) {
  const db = firestoreDb();
  return collection(db, 'workspaces', workspaceId, 'boards', boardId, 'cards');
}

export async function listCards(workspaceId: string, boardId: string): Promise<CardDoc[]> {
  const q = query(cardsRef(workspaceId, boardId), orderBy('order', 'asc'));
  const snap = await getDocs(q);
  return snap.docs.map((d) => ({ id: d.id, ...(d.data() as Omit<CardDoc, 'id'>) }));
}

export async function createCard(
  workspaceId: string,
  boardId: string,
  input: Omit<CardDoc, 'id' | 'createdAt' | 'updatedAt' | 'boardId'>
) {
  // Avoid composite-index requirements: default to time-based ordering.
  // Reordering can override this later.
  const ref = await addDoc(cardsRef(workspaceId, boardId), {
    ...input,
    boardId,
    order: Date.now(),
    createdAt: Date.now(),
    updatedAt: Date.now(),
  });

  return ref.id;
}

export async function updateCard(
  workspaceId: string,
  boardId: string,
  cardId: string,
  patch: Record<string, unknown>
) {
  const db = firestoreDb();
  await updateDoc(doc(db, 'workspaces', workspaceId, 'boards', boardId, 'cards', cardId), {
    ...patch,
    updatedAt: Date.now(),
  });
}

export async function deleteCard(workspaceId: string, boardId: string, cardId: string) {
  const db = firestoreDb();
  await deleteDoc(doc(db, 'workspaces', workspaceId, 'boards', boardId, 'cards', cardId));
}
